const getDataObject = (formData, attributes = []) => {
  const obj = {}

  // eslint-disable-next-line no-restricted-syntax
  for (const attribute of attributes) {
    const item = formData.get(attribute)
    if (![undefined, null, '', {}, []].includes(item)) {
      obj[attribute] = item
    }
  }

  return obj
}

export default {
  getDataObject
}